import {playMode} from 'common/js/config'
import {loadSearch,loadPlay,loadFavorite} from 'common/js/cache'



const state = {
  singer:{},
  playing:false, //
  fullScreen:false, //
  playList:[], // [Song,Song]
  sequenceList:[], // [Song,Song]
  mode:playMode.sequence, //
  currentIndex:-1, //
  disc:{},
  topMusic:{},
  searchHistory: loadSearch(), // [string,string...]
  playHistory:loadPlay(), // player ready[object,object]
  favoriteList:loadFavorite()
};

export default state



