
import originJsonp from 'jsonp'

export default function jsonp(url,data,options) {
  url += (url.indexOf('?')<0 ? '?' : '&') + param(data); //url&jsonpCallback=_jp0
  //url = url + url?'?''&' + 
  return new Promise((resolve,reject) =>{
    originJsonp(url,options,(err,data) => { //option:{ param:'jsonpCallback' } jsonp
      if(!err){
        resolve(data)
      }else{
        reject(err)
      }
    })
  } )
}

function param(data) {       //
  let url = '';
  for(let k in data){
    let value = data[k] !== undefined ? data[k] : '';
    url +='&'+ k +'='+encodeURIComponent(value)
  }
  //console.log(url) //&g_tk=5381&inCharset=utf-8&outCharset=utf-8&notice=0&format=jsonp&platform=h5&uin=0&needNewCode=1
  return url ? url.substr(1) : '';
}



